
import XHeader from 'vux/src/components/x-header/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default {
name: 'login-view',
components: { XHeader, Grid, GridItem, XButton, Popup },
data () {
return {
token: localStorage.getItem('accountToken'),
showActionSheet: false,
menus: {
menu: '退出登录'
},
websocket: '',
commodity_list: [1, 2]
}
},
created () {
this.init()
},
mounted () {
const that = this
window.onbeforeunload = function () {
that.websocket.close()
}
},
destroyed: function () {
this.websocket.close()
},
methods: {
init () {

if ('WebSocket' in window) {
this.websocket = new WebSocket('ws://quote.vs.com:8888')
}

const that = this
this.websocket.onmessage = function (event) {
if (JSON.parse(event.data).method === 'on_rsp_commodity_list') {
that.commodity_list = JSON.parse(event.data).data.commodity_list
}
}
this.websocket.onclose = function (event) {
that.websocket = new WebSocket('ws://quote.vs.com:8888')
}
this.websocket.onopen = function (event) {
that.websocket.send(JSON.stringify({
'method': 'req_login',
'req_id': '1231',
'data': {
'user_name': 'chenlin',
'password': 'a123456',
'protoc_version': '2.0'
}
}))
that.websocket.send(JSON.stringify({
'method': 'req_commodity_list',
'req_id': '1231',
'data': {}
}))
}
},
goLogin () {
this.$router.push({path: '/login'})
},
loginOut () {
this.showActionSheet = false
this.$store.commit('clearAccountToken')
this.$router.push({path: '/login'})
}
}
}



